// @flow

/**
 * UrlTopNextPages.js - действия и генераторы действий для добавления данных о топе источников
 * @module actions/UrlTopNextPages
 * @requires module:config
 * @requires module:constants/UrlTopNextPages
 * @requires module:common/index
 * @requires module:api/index
 * */

import {SET_TOP_NEXT_PAGES} from '../constants/UrlTopNextPages'
import {interval} from '../common'
import {POLLING_INTERVAL, dataProviderTypes} from '../config'
import {getAPI} from '../api'
const api = getAPI(dataProviderTypes.LINKPULSE, (api) => api);

/**
 * @memberof actions/UrlTopNextPages
 * */
type UrlTopNextPages = [{
    value: number,
    title: string,
    url: string,
    percent: number
}]

/**
 * @memberof actions/UrlTopNextPages
 * */
type UrlTopNextPagesAction = {
    type: string,
    urlTopNextPages: UrlTopNextPages
}

/**
 * @memberof actions/UrlTopNextPages
 * */
export function setUrlTopNextPages(urlTopNextPages: UrlTopNextPages): UrlTopNextPagesAction {
    return {
        type: SET_TOP_NEXT_PAGES,
        urlTopNextPages: urlTopNextPages
    }
}

/**
 * Запрашиваем топ источников через API в интервале
 * @memberof actions/UrlTopNextPages
 * */
export function fetchUrlTopNextPages(maxArticlesCount: number, url?: string = '') {
    return (dispatch: any) => {
        interval(() => {
            api.fetchUrlTopNextPages(maxArticlesCount, url).then((urlTopNextPages: UrlTopNextPages) => {
                dispatch(setUrlTopNextPages(urlTopNextPages))
            }).catch((error) => {
                console.log(error)
            })
        }, POLLING_INTERVAL, true, 'fetchUrlTopNextPages')
    }
}



