// @flow

/**
 * Card.js - компонент карточки
 * @module components/Card
 * */

import React from 'react'
import moment from 'moment'

import {LoadingIndicator} from '../../components/LoadingIndicator/LoadingIndicator'

import './Card.css'

/**
 * @memberof components/Card
 * */
type Props = {
    title: string,
    url: string,
    image: string,
    description?: string,
    author?: string,
    date?: string,
    counters?: [{
        value: number,
        title: string,
    }],
};

/**
 * @namespace
 * */
export const Card = (props: Props) => {
    const {title, url, image, description, author, date, counters} = props
    
    const isNotLoading = !title && !url
    
    const Loading = (
        <LoadingIndicator/>
    )
    
    /**
     * Отрисовка компонента при изменении состояния
     * @memberof Card
     * @alias render
     * */
    return (
    <div className="Card">
        <div className="card">
            <div className="card-body">
                {(!isNotLoading) ? '' : Loading}
                <h4 className="card-title">
                    {url && (<a href={url} className="card-link" target="_blank"><i className="material-icons">open_in_new</i></a>)}
                    <span className="Card__title">{title}</span>
                    </h4>
                {description && (<p className="card-text">{description}</p>)}
            </div>
            <ul className="list-group list-group-flush">
                {author && (<li className="list-group-item">{author}</li>)}
                {date && (<li className="list-group-item">{moment(date).format('DD MMMM HH:mm')}</li>)}
                {counters && (<li className="list-group-item p-0 border-0">{Object.keys(counters).map((key, index) => <span key={index} className={`Card__counter Card__counter--${counters[key].id}`} title={counters[key].title}>{counters[key].value}</span>)}</li>)}
            </ul>
            <img className="card-img-bottom" src={image} alt="" />
        </div>
    </div>
    )
}



