// @flow

/**
 * Backlink.js - компонент ссылки назад
 * @module components/Backlink
 * */

import React from 'react'
import {Link} from 'react-router-dom'

import './Backlink.css'

/**
 * @namespace
 * */
export const Backlink = () => {
    /**
     * Отрисовка компонента при изменении состояния
     * @memberof Backlink
     * @alias render
     * */
    return (
    <div className="Backlink">
        <div className="list-group">
            <Link className="list-group-item list-group-item-action" to="/"><i className="material-icons">keyboard_backspace</i> Назад</Link>
        </div>
    </div>
    )
}



