// @flow

/**
 * PageInfoActions.js - действия и генераторы действий для добавления данных о странице
 * @module actions/PageInfoActions
 * @requires module:config
 * @requires module:constants/PageInfo
 * @requires module:common/index
 * @requires module:api/index
 * */

import {SET_PAGE_INFO} from '../constants/PageInfo'
import {interval} from '../common'
import {POLLING_INTERVAL, dataProviderTypes} from '../config'
import {getAPI} from '../api'
const api = getAPI(dataProviderTypes.LINKPULSE, (api) => api);

/**
 * @memberof actions/PageInfo
 * */
type PageInfo = {
    count: number,
    viewtime: number,
    recirculation: number,
    title: string,
    url: string,
    image: string,
    author?: string,
    description?: string,
    date?: string,
}

/**
 * @memberof actions/PageInfo
 * */
type PageInfoAction = {
    type: string,
    pageInfo: PageInfo
}

/**
 * @memberof actions/PageInfo
 * */
export function setPageInfo(pageInfo: PageInfo): PageInfoAction {
    return {
        type: SET_PAGE_INFO,
        pageInfo: pageInfo
    }
}

/**
 * Запрашиваем информацию о странице
 * @memberof actions/PageInfo
 * */
export function fetchPageInfo(url: string) {
    return (dispatch: any) => {
        interval(() => {
            api.fetchPageInfo(url).then((PageInfo: PageInfo) => {
                dispatch(setPageInfo(PageInfo))
            }).catch((error) => {
                console.log(error)
            })
        }, POLLING_INTERVAL, true, 'fetchPageInfo')
    }
}



