// @flow

/**
 * Page.js - контейнер для страницы дэшборда
 * @module containers/Page
 * @requires module:components/Nav
 * @requires module:components/Counter
 * @requires module:components/List
 * */

import React, {Component} from 'react'
import {bindActionCreators} from 'redux'
import {connect} from 'react-redux'
import ReactTooltip from 'react-tooltip'

import {Layout} from '../../components/Layout/Layout'
import {Counter} from '../../components/Counter/Counter'
import {List} from '../../components/List/List'
import {Card} from '../../components/Card/Card'
import {Backlink} from '../../components/Backlink/Backlink'
import {Chart} from '../../containers/Chart/Chart'

import {clearAllIntervals} from '../../common'

import * as pageInfoActions from '../../actions/PageInfoActions'
import * as usersTimelineActions from '../../actions/UsersTimelineActions'
import * as topReferersActions from '../../actions/TopReferersActions'
import * as urlTopNextPagesActions from '../../actions/UrlTopNextPages'
import * as devicesActions from '../../actions/DevicesActions'

import './Page.css'

/**
 * @memberof containers/Page
 * */
type Timeline = {
    date: string,
    today: number,
    weekAgo: number,
}

/**
 * @memberof containers/Page
 * */
type Props = {
    usersTimelineActions(): {
        fetchUsersTimeline(): any,
    },
    pageInfoActions(): {
        fetchPageInfo(): any,
    },
    usersTimeline: {
        timeline: Timeline[],
    },
    topReferersActions(): {
        fetchTopReferers(): any,
    },
    urlTopNextPagesActions(): {
        fetchUrlTopNextPages(): any,
    },
    devicesActions(): {
        fetchDevices(): any,
    },
    topReferers: {
        topReferers: [{
            value: number,
            title: string,
            url: string,
            percent: number
        }]
    },
    urlTopNextPages: {
        urlTopNextPages: [{
            value: number,
            title: string,
            url: string,
            percent: number
        }]
    },
    pageInfo: {
        count: number,
        viewtime: number,
        recirculation: number,
        title: string,
        url: string,
        image: string,
        description?: string,
        author?: string,
        date?: string,
    },
    devices: [{
        value: number,
        title: string,
        percent: number,
        url: string,
    }],
    match: {
        params: {
            url: string
        }
    },
}

const info = {
    realtime: 'Количество активных пользователей в данный момент',
    viewtime: 'Среднее время чтения за последнюю минуту',
    recirculation: 'Отношение количества кликов к количеству активных пользователей за последнюю минуту',
}

/**
 * @namespace Page
 * */
export class Page extends Component<Props, void> {
    /**
     * */
    static props:Props
    
    /**
     * Получаем данные после отрисовки
     * */
    componentDidMount() {
        this.fetchData()
    }
    
    /**
     * Получаем данные после отрисовки
     * */
    componentDidUpdate(prevProps) {
        const prevUrl = decodeURIComponent(prevProps.match.params.url)
        const url = decodeURIComponent(this.props.match.params.url)
        
        if (prevUrl !== url) {
            this.fetchData()
        }
    }
    
    /**
     * Отключаем компонент
     * */
    componentWillUnmount() {
        clearAllIntervals()
    }
    
    /**
     * Отрисовка компонента при изменении состояния
     * */
    render() {
        const {pageInfo, usersTimeline, devices} = this.props
        const {topReferers} = this.props.topReferers
        const {urlTopNextPages} = this.props.urlTopNextPages

        return (
        <Layout>
            <div className="Page">
                <div className="row">
                    <div className="col-3 p-0 Page__sidebar">
                        <div className="Page__tiles">
                            <div className="Page__tile Page__tile--col-3" data-tip={info.realtime}>
                                <Counter count={pageInfo.count} title="активных пользователей" theme="blue" data-tip="test"/>
                            </div>
                            <div className="Page__tile Page__tile--col-3" data-tip={info.viewtime}>
                                <Counter count={pageInfo.viewtime} title="среднее время чтения" theme="green"/>
                            </div>
                            <div className="Page__tile Page__tile--col-3" data-tip={info.recirculation}>
                                <Counter count={pageInfo.recirculation} title="% рециркуляции" theme="purple"/>
                            </div>
                        </div>
                        <div className="Page__sidebarList">
                            <List title="Источники" data={topReferers} size="small"/>
                        </div>
                        <div className="Dashboard__sidebarList">
                            <List title="Устройства" data={devices} size="small"/>
                        </div>
                    </div>
                    <div className="col-6 Page__content">
                        <Chart data={usersTimeline.timeline} title="Пользователей сегодня"/>
                        <Backlink />
                        <Card {...pageInfo} />
                    </div>
                    <div className="col-3 p-0 Page__secondSidebar">
                        <List title="Следующие страницы" data={urlTopNextPages} size="small"/>
                    </div>
                </div>
        
                <ReactTooltip place="right" type="dark" effect="solid"/>
            </div>
        </Layout>
        )
    }
    
    /**
     * */
    fetchData() {
        const {fetchPageInfo} = this.props.pageInfoActions
        const {fetchUsersTimeline} = this.props.usersTimelineActions
        const {fetchTopReferers} = this.props.topReferersActions
        const {fetchUrlTopNextPages} = this.props.urlTopNextPagesActions
        const {fetchDevices} = this.props.devicesActions
    
        const url = decodeURIComponent(this.props.match.params.url)
    
        fetchPageInfo(url)
        fetchUrlTopNextPages(10, url)
        fetchUsersTimeline(url)
        fetchTopReferers(10, url)
        fetchDevices(url)
    }
}

function mapStateToProps(state) {
    return {
        pageInfo: state.pageInfo,
        usersTimeline: state.usersTimeline,
        topReferers: state.topReferers,
        urlTopNextPages: state.urlTopNextPages,
        devices: state.devices,
    }
}

function mapDispatchToProps(dispatch) {
    return {
        pageInfoActions: bindActionCreators(pageInfoActions, dispatch),
        usersTimelineActions: bindActionCreators(usersTimelineActions, dispatch),
        topReferersActions: bindActionCreators(topReferersActions, dispatch),
        urlTopNextPagesActions: bindActionCreators(urlTopNextPagesActions, dispatch),
        devicesActions: bindActionCreators(devicesActions, dispatch),
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Page)



